

main {
	padding-top: 4rem;
	z-index: -100;
	position: relative;

}

.main__container-img {
	display: flex;
	justify-content: center;
	align-items: center;
}

.main__img {
	height: 300px;
}

.main__container-text {
	display: flex;
	flex-direction: column;
}

.main__title {
	/*color: var(--color-white);*/
	margin: 1rem 0 0;
	/*font-size: 1.25rem;*/
	text-align: center;
	font-size: 1.75rem;
	font-family: "Tilt Neon", serif;
	text-shadow:
			0 0 5px #ffa500,
			0 0 15px #ffa500,
			0 0 20px #ffa500,
			0 0 40px #ffa500,
			0 0 60px #ff0000,
			0 0 10px #ff8d00,
			0 0 98px #ff0000;
	color: #fff6a9;
}

.main__subtitle {
	color: var(--color-secondary);
	margin: 1rem auto;
	font-size: 1rem;
	width: 275px;
	line-height: 1.8;
	text-align: center;
}

.main__container-cards {
	display: flex;
	/*flex-direction: column;*/
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
}

.main__container-cards {
	margin: 1.5rem 0;
}

.main__container-text a {
	color: var(--color-white);
	background: inherit;
	border: 1px solid var(--color-white);
	width: max-content;
	padding: 7.5px 25px;
	margin: 0.5rem auto 0;
	cursor: pointer;
	transition: all 0.5s;
	text-decoration: none;
}

.main__container-text a:hover {
	color: var(--color-dark-);
	background-color: var(--color-white);
	cursor: pointer;
}

@media all and (min-width: 550px) {
	main {
		margin-top: 1rem;
		z-index: -100;
		position: relative;
		user-select: none;

	}

